.about-me-main {
  display: flex;
  flex-direction: row;

  margin: 20px;

  height: 520px; /* Adjust this height to the size you want */
  overflow-y: auto; /* Enable vertical scrolling for the entire section */
  overflow-x: hidden; /* Prevent horizontal scrolling */
  overflow: -moz-scrollbars-none; /* For Firefox */
  -ms-overflow-style: none; /* For Internet Explorer and Edge */
  scrollbar-width: none; /* For Firefox */
}
.about-me-title {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.about-me-main-title {
  color: rgb(18, 18, 18);
  font-family: "system-ui";
  font-weight: 800;
  font-size: x-large;
}
.underline {
  width: 60px;
  height: 8px;
  background-color: #00b0d7;
  border-radius: 50px;
}

.about-me-desc {
  flex: 2;
  display: flex;
  flex-direction: row;
  /* padding-top: 10px; */
  font-family: "system-ui";
}
.circles-container {
  margin-top: 10px;
}
.circles {
  width: 15px;
  height: 15px;
  border: 2px solid #00b0d7;
  border-radius: 50%;
  background-color: white;
  position: relative;
}

.circles::before {
  content: "";
  position: relative;
  width: 2px;
  height: 30px;
  background-color: #00b0d7;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}
.circle-line {
  width: 1px;
  height: 30px;
  background-color: gray;
  margin: 0 auto;
  position: relative;
}
.desc-container {
  margin-left: 20px;
}
.history-title {
  font-weight: 800;
  font-size: larger;
  margin-top: 4px;
}
.remove-dots {
  list-style-type: none;
  font-size: large;
}
.remove-dots li {
  margin-top: 10px;
  margin-bottom: 10px;
}
.remove-dots li::marker {
  color: #00b0d7;
}
.top-margin-title {
  margin-top: 30px;
}

@media only screen and (max-width: 720px) {
  .about-me-main {
    display: flex;
    flex-direction: column;

    margin: 20px;
    /* background-color: aquamarine; */
    height: 520px; /* Adjust this height to the size you want */
    overflow-y: auto; /* Enable vertical scrolling for the entire section */
    overflow-x: hidden; /* Prevent horizontal scrolling */
    overflow: -moz-scrollbars-none; /* For Firefox */
    -ms-overflow-style: none; /* For Internet Explorer and Edge */
    scrollbar-width: none; /* For Firefox */
  }
  .about-me-title {
    flex: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 30px;
  }
  .about-me-main-title {
    color: rgb(18, 18, 18);
    font-family: "system-ui";
    font-weight: 800;
    font-size: x-large;
  }
  .underline {
    width: 60px;
    height: 8px;
    background-color: #00b0d7;
    border-radius: 50px;
  }

  .about-me-desc {
    flex: 2;
    display: flex;
    flex-direction: row;
    /* padding-top: 10px; */
    font-family: "system-ui";
    /* background-color: red; */
  }
  .circles-container {
    margin-top: 10px;
    display: none;
  }
  .circles {
    width: 15px;
    height: 15px;
    border: 2px solid #00b0d7;
    border-radius: 50%;
    background-color: white;
    position: relative;
  }

  .circles::before {
    content: "";
    position: relative;
    width: 2px;
    height: 30px;
    background-color: #00b0d7;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
  }
  .circle-line {
    width: 1px;
    height: 30px;
    background-color: gray;
    margin: 0 auto;
    position: relative;
  }
  .desc-container {
    margin-left: 0px;
  }
  .history-title {
    font-weight: 800;
    font-size: larger;
    margin-top: 0px;
  }
  .remove-dots {
    list-style-type: none;
    font-size: large;
  }
  .remove-dots li {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .remove-dots li::marker {
    color: #00b0d7;
  }
  .top-margin-title {
    margin-top: 30px;
  }
}
